footer {
    margin-left: 15%;
    position: absolute;
    position: fixed;
    top: 90vh;
    width: 85%;
    height: 10vh;
    display: flex;
    box-shadow: 0 0.5rem 1rem gray;
    align-items: center;
    background: rgb(255, 255, 255);
}

.footer_left {
    margin-left: 5%;
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    gap: 1em;
}

.footer_left ul {
    list-style: none;
    display: flex;
    gap: 1em;
}

.footer_left ul li {
    font-weight: bold;
    cursor: pointer;
    font-size: 15px;
}

.footer_left p {
    color: gray;
}

.language {
    display: flex;
    color: gray;
}

.footer_right {
    display: flex;
    margin-left: auto;
    margin-right: 5%;
    gap: 2em;
}

.footer_right p {
    margin-top: auto;
    margin-bottom: auto;
    cursor: pointer;
}


@media (max-width: 1200px) {
    footer {
        margin-left: 5%;
        width: 95%;
    }
}

@media (max-width: 975px) {
    .footer_left p {
        display: none;
    }
}

@media (max-width: 600px) {
    footer {
        margin-left: 10%;
        width: 90%;
    }

    .footer_left ul {
        display: none;
    }

    .footer_left p {
        display: flex;
    }
}

@media (max-width: 500px) {
    footer {
        display: none;
    }
}